@use 'setting' as f;
@import url("https://fonts.googleapis.com/css?family=Montserrat:100,100i,200,200i,300,300i,400,400i,500,500i,600,600i,700,700i,800,800i,900,900i&display=swap&subset=cyrillic,cyrillic-ext,latin-ext");

:root[data-theme=light]{
  --primary-color: #{f.$primary-color-light};
  --on-primary-color: #{f.$on-primary-color-light};

  --background-color: #{f.$background-color-light};
  --on-background-color: #{f.$on-background-color-light};

  --element-color: #{f.$element-color-light};
  --on-element-color: #{f.$on-element-color-light};

  --error-color: #{f.$error-color-light};
  --on-error-color: #{f.$on-error-color-light};

  --hovered: #{f.$light-hovered};
  --focused: #{f.$light-focused};
  --pressed: #{f.$light-pressed};
}


:root[data-theme=dark] {
  --primary-color: #{f.$primary-color-dark};
  --on-primary-color: #{f.$on-primary-color-dark};

  --background-color: #{f.$background-color-dark};
  --on-background-color:#{f.$on-background-color-dark};

  --element-color: #{f.$element-color-dark};
  --on-element-color: #{f.$on-element-color-dark};

  --error-color: #{f.$error-color-dark};
  --on-error-color: #{f.$on-error-color-dark};

  --hovered: #{f.$dark-hovered};
  --focused: #{f.$dark-focused};
  --pressed: #{f.$dark-pressed};
}

:root {
  --primary-variant-color: #{f.$primary-variant-color};

  --secondary-color: #{f.$secondary-color};
  --on-secondary-color: #{f.$on-secondary-color};

  --only-light: #{f.$light-00dp};
  --only-dark: #{f.$dark-00dp};

  --nav-height: 5rem;
}

:root::-webkit-scrollbar {
  overflow: auto;
  width: 0.5rem;
  height: 0.5rem;
  background-color: transparent;
  
}
:root::-webkit-scrollbar-thumb {
  background-color: rgb(127, 127, 127);
  border-radius: 5px;
}

:root {
  scrollbar-color: rgb(127, 127, 127) transparent;
  scrollbar-width: thin;
}

* {
  margin: 0;
  padding: 0;
}

html {
  font-size: 16px;
  line-height: 1.5;

  scroll-behavior: smooth; 
}

html[data-theme=light] {
  color-scheme: light;
}

html[data-theme=dark] {
  color-scheme: dark;
}

body {
  color: var(--on-background-color);
  font-family: f.$font-family;
  line-height: inherit;

  background-color: var(--background-color);

  overscroll-behavior: none;
  scrollbar-gutter: stable;
}

main {
  min-height: calc(100vh - 5rem);
  box-sizing: border-box;
  padding: 0 20px;
}

#root {
  max-width: f.$container-max-widths-xll;
  margin: 0 auto;
}

h1, h2, h3, h4, h5, h6 {
  text-wrap: balance;
}

